<template lang="">
    <div class="inventory">
        <div class="container">
            <div class="inventory-main">
                <div class="inventory-main_filters">sss</div>
                <div class="inventory-main_search">
                    <input type="text" v-model="search" class="inventory-search" placeholder="Find a dream car...">
                </div>
                <div class="inventory-main_cars">
                    <div class="inventory-main_car">
                        <div class="inventory-card" v-for="(inventoryCar, i) in searchHandler" :key="i">
                        <div class="inventory-card_top">
                            <swiper
                                :scrollbar="{
                                hide: false,
                                }"
                                :modules="modules"
                                class="mySwiper6"
                            >
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image2">
                                </swiper-slide>
                                <swiper-slide class="inventory-slide">
                                    <img :src="inventoryCar.exterior.image3">
                                </swiper-slide>
                            </swiper>
                        </div>
                        <div class="inventory-card_bottom" @click="cartClick(inventoryCar.id)">
                            <h3>{{inventoryCar.titleCar}}</h3>
                            <p>$ {{inventoryCar.price}}</p>
                            <div class="cars-information">
                                <span>{{inventoryCar.year}} year</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.body}}</span>
                                <img src="../assets/img/inventory/Vector.png">
                                <span>{{inventoryCar.transmission}}</span>
                            </div>
                            <span>{{inventoryCar.kilometres}}</span>
                        </div>
                    </div>
                </div>
                    <div class="btn-toolbar">
                        <div class="btn-group">
                            <button v-for="p in totalPaginas()" @click = "getDataPaginas(p)" class="buttonP" 
                            :class="isActive(p)">
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from "swiper/vue";

// Import Swiper styles
import "swiper/css";

import "swiper/css/scrollbar";

// import required modules
import { Scrollbar } from "swiper";

export default {
    data(){
        return{
            perPage: null,
            dataPagination: [],
            paginaActual: 1,
            search: '',
        }
    },

    computed: {
        inventoryCars(){
            return this.$store.getters.inventoryCars
        },
        searchHandler(){
            return this.dataPagination.filter(elem => {
                return elem.titleCar.toUpperCase().includes(this.search.toUpperCase())
            })
        }
    },

    methods:{
        innerWidth(){
            this.width = window.innerWidth;
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
        },
        totalPaginas(){
            return Math.ceil(this.inventoryCars.length / this.perPage) 
        },
        getDataPaginas(noPagina){
            this.dataPagination = []
            this.paginaActual = noPagina
            let ini = (noPagina * this.perPage) - this.perPage
            let fin = (noPagina * this.perPage)
            this.dataPagination = this.inventoryCars.slice(ini, fin) 
            window.scrollTo(0,0)           
        },
        cartClick(id){
            this.$router.push({name: 'InventoryCar', query: {'inventory': id}})
        },
        isActive(noPagina){
            return noPagina == this.paginaActual ? 'activeP' : ''
        }
    },

    created() {
        window.addEventListener('resize', this.innerWidth);
        this.innerWidth();
    },

    mounted() {
        this.getDataPaginas(1);
    },

    watch:{
        perPage(){
            if(this.width > 576){
                this.perPage = 6
            }
            if(this.width <= 576){
                this.perPage = 4
            }
            this.getDataPaginas(1)
        }
    },

    components: {
        Swiper,
        SwiperSlide,
    },
    setup() {
        return {
        modules: [Scrollbar],
        };
    },
    
}
</script>
<style lang="scss">

@import '../assets/scss/breakpoints.scss';

.inventory{
    margin-top: 40px;
}
.inventory-main{
    display: grid;
    grid-template-columns: 30% 70%;
    grid-template-areas: 'filters search' 'filters cars';
}

.inventory-main_filters {
    grid-area: filters;
}


                                            //Search

.inventory-main_search {
    grid-area: search;
    margin-bottom: 20px;
}
.inventory-search{
    width: 350px;
    height: 45px;
    font-family: 'Gilroy500';
    font-size: 16px;
    line-height: 159%;
    color: #606276;
    padding: 11px 20px;
    background-image: url(../assets/img/inventory/Search.png);
    background-position: right 20px center;
    background-repeat: no-repeat;
    border: 1px solid #D7D7D7;
    border-radius: 2px;
}


.inventory-main_cars {
    grid-area: cars;
    display: flex;
    flex-direction: column;
    gap: 40px;

    .inventory-main_car{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 20px; 
    }
}
.inventory-card {
    width: 48%;
}
.inventory-card_top {
}
.mySwiper6 {

    .swiper-scrollbar-horizontal{
        position: absolute;
        left: 5%;
        bottom: 10px;
        width: 90%;
        background: hsla(0,0%,100%,.2);
    }
    .swiper-scrollbar-drag{
        background: #FFFFFF;
        opacity: 0.7;
        border-radius: 28px;
    }
    .swiper-wrapper{
        gap: 0;
    }
}
.inventory-slide {

    img{
        width: 100%;
        aspect-ratio: 1/.515;
        object-fit: cover;
    }
}
.inventory-card_bottom {
    padding: 20px;
    box-shadow: 0px 20px 25px rgba(0, 0, 0, 0.1);
    border-radius: 0px 0px 5px 5px;
    cursor: pointer;

    h3, p{
        font-family: 'Gilroy600';
        font-size: 20px;
        line-height: 25px;
        color: #41456B;
    }
    p{
        color: #7481FF;
        margin-top: 10px;
    }
    span{
        font-family: 'Gilroy400';
        font-size: 16px;
        line-height: 180%;
        letter-spacing: 0.02em;
        color: #41456B;
    }
}
.cars-information {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 20px;
    margin-top: 20px;
}


                                            //pagination

                                    
.btn-toolbar {
    padding-bottom: 40px;
    margin: 0px auto 0;
    @include breakpoints(sm){
        margin: 0px auto 0;
        padding-bottom: 30px;
    }
}
.btn-group {
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;

    .activeP{
        opacity: 1;
        transition: .3s opacity ease;
    }
}  

.buttonP{
    width: 10px;
    height: 10px;
    background: #606276;
    opacity: 0.3;
    border-radius: 50%;
    cursor: pointer;
}
    
</style>